#header {
    width: 100%;
    height: 70px;
    z-index: 90;
    padding: 1em 4.875em;
    background-color: #fff;
    box-shadow: 0 1px 1px #ccc;
    position: fixed;
    display: flex;
}
body{
    background-color: #f2f6ff;
    
}

#logo_div{
    display: block;
}
#logo{
    font-size: 32px ;
    color:#0084ff; 
    font-weight: 900;
    font-family: 'Open Sans', sans-serif;
    width: 130px;
    margin: 0px;
}
#h3_tag{
    margin-top: 0px;
    font-family: 'Raleway', sans-serif;
    font-style: italic;
    margin:0;
    font-weight: bold;
    font-size: 18px;
}
#para_id{
    display: flex;
    
}
#para_id img {
    font-size:14px;
    padding-top: 0px;
    padding-left: 8px;
    height:10px;
    width:15px;
    margin-top: 22px;
    

}
#h2_tag{
    font-size:16px;
    color: #000;
    text-align: center;
    margin-left: 65px;
    font-family: 'Open Sans', sans-serif;
    margin-top: 15px;
}
#btn{
    width: auto;
    font-size: 0.75em;
    padding: 0.5em 2em;
    margin: 0px 1em 0px 0px;
    box-shadow: none;
    background: linear-gradient(269deg,#0048ff,#0096ff);
    min-width: 136px;
    height: 42px;
    border-radius: 35px;
    color: #fff;
    border: none;
    font-family: 'Raleway',sans-serif;
   

}
#para{
    font-size: 17px;
    font-weight: 500;
    line-height: 1;
    letter-spacing: .4px;
    text-align: left;
    color: rgba(0, 0, 0, 0.345);
    margin-top: 0.5em;
    font-family: 'Raleway',sans-serif;
    margin-right:30px ;
}


